*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
.box{
    width: 100%;
    height: 100vh;
    background-image: url("./image/map.jpeg");
    background-position: center;
    background-color: rgba(0,0,0, 0.2);
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.box2{
    width: 35%;
    height: 90vh;
    background-image: linear-gradient(to bottom, rgba(20, 20, 20, 0.2), rgba(0, 0, 0, 0.75));
    box-shadow: 5px 5px 25px 5px rgb(150, 156, 238);
    border-radius: 5px;
}
.inputdata{
    width: 100%;
  margin: 20px 20px 25px;
}
.inputdata .inputf{
    display: block;
    text-align: center;
    width: 90%;
    padding: 15px;
    background: none;
    border: none;
    outline: none;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 0px 0px 16px 16px;
    box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
    color: #313131;
    font-size: 20px;
    transition: 0.4s ease;
}
.info{
    text-align: center;
}
.info .city{
    color: #FFF;
    font-size: 52px;
    font-weight: 900;
    text-transform: uppercase;
    text-shadow: 3px 3px rgba(50, 50, 70, 0.5); 
}
.info .date{
    color: #FFF; 
    margin-top: 9px;
    margin-bottom: 9px;
    font-size: 26px;
    font-weight: 500;
    font-style: italic;
    text-align: center;
    text-transform: uppercase;
    text-shadow: 2px 2px rgba(56, 56, 78, 0.5);
}
.info .time{
    color: #FFF; 
    font-size: 24px;
    font-weight: 500;
}
.info .tempt{
    position: relative;
    display: inline-block;
    margin: 30px auto;
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    padding: 15px 25px;
    color: #FFF;
    font-size: 30px;
    font-weight: 900;
    text-shadow: 2px 3px rgba(50, 50, 70, 0.5);
    text-align: center;
    box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}
.info .weather {
    color: #FFF;
    font-size: 24px;
    font-weight: 100;
    text-transform: uppercase;
    text-shadow: 3px 6px rgba(50, 50, 70, 0.5); 
}
.info .icon img{
    size: 40px;
    margin-top: 5px;
}
.info .limits{
    color: rgb(31, 98, 243);
    font-size: 22px;
    font-weight: 500;
    margin-top: 13px;
}
   
  
  